.fulsivern__whatFulsivern {
    display: flex;
    flex-direction: column;
    padding: 2rem;

    background: var(--color-footer);

    background:-moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
    background:-webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
    background:-o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
    background:-ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
    background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.fulsivern__whatFulsivern-feature {
    display: flex;

}

.fulsivern__whatFulsivern-feature 
.fulsivern__features-container__feature {
    margin: 0;
}

.fulsivern__whatFulsivern-feature 
.fulsivern__features-container__feature-text {
    max-width: 700px;
}

.fulsivern__whatFulsivern-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    margin: 4rem 0 2rem;
}

.fulsivern__whatFulsivern-heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    max-width: 510px;
}

.fulsivern__whatFulsivern-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}

.fulsivern__whatFulsivern-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    margin-top: 2rem;
}


.fulsivern__whatFulsivern-container 
.fulsivern__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.fulsivern__whatFulsivern-container 
.fulsivern__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 1024px) {
    .fulsivern__whatFulsivern-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .fulsivern__whatFulsivern-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 768px) {
    .fulsivern__whatFulsivern-feature 
    .fulsivern__features-container__feature {
        flex-direction: column;
    }

    .fulsivern__whatFulsivern-feature 
    .fulsivern__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .fulsivern__whatFulsivern-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 600px) {
    .fulsivern__whatFulsivern-container 
    .fulsivern__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}