.fulsivern__blog-container_article {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.fulsivern__blog-container_article-image {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.fulsivern__blog-container_article-image img {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.fulsivern__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;
}

.fulsivern__blog-container_article-content p {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 12px;
    line-height: 35px;
    color: white;
}

.fulsivern__blog-container_article-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 30px;
    color: white;

    margin-bottom: 5rem;
    cursor: pointer;
}

.fulsivern__blog-container_article-content p:last-child {
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .fulsivern__blog-container_article {
        font-size: 18px;
        line-height: 25px;
    }
}